import Button from 'react-bootstrap/Button';
import axios from "axios";
import { fetch } from "../_scripts/communications";
import  Spinner  from 'react-bootstrap/Spinner';

//Thisd version of the submit button is used when things will take a bit. Its got human feedback basically to show its working

export default function LongSubmit(p){

    const {dispatch,endpoint,data,color,text,showspin} = p;

    return (
        <div>
        <Button 
            className='mb-3'
            variant={color} 
            onClick={async()=>{
                dispatch({type:'submit-Clicked',color:'primary disabled', text:'working'});
                let response = await fetch({method:"POST",endpoint:endpoint,data:data});
                if(response.didError){
                    //What to do if error
                    console.log(response);

                    dispatch({type:'submit-Error', color:'danger', text:'error', errorText:''})

                    if(response.data.dispatches !== undefined){
                        response.data.dispatches.map((d)=>dispatch(d))
                    }

                    return
                }
                
                dispatch({type:'submit-Success', color:'success', text:'Success'})

                if(response.data.dispatches !== undefined){
                    response.data.dispatches.map((d)=>dispatch(d))
                }

                return
            }}>
            {text}
        </Button>
        <Spinner animation="border" role="status" variant='warning' className='ms-1' hidden={!showspin}>
            <span className="visually-hidden">Loading...</span>
        </Spinner>
        </div>

    )

};