'use client'
import styles from "../../page.module.css";
import Form from 'react-bootstrap/Form';
import EntryBox from "../../_components/entrybox";
import Submit from "../../_components/submit";
import Select from "../../_components/select";
import { useEffect, useReducer } from "react";
import CheckBox from "../../_components/checkbox";
import { fetch } from "../../_scripts/communications";
import backgroundImage from "../../../public/Green.png"
import FormTitle from "@/app/_components/formtitle";
import Feedback from "@/app/_components/feedback";
import LongSubmit from "@/app/_components/longsubmit";
import FormDescription from "@/app/_components/formdescription";

let pageDescription = ['Create a new project for the employeeâ€™s training plan in Rent Manager','and assign them the corresponding RMU learning paths.']

let submitObject = {};

const initState = {
    'Select Path':{
        options: [],
        value:0
    },
    'Assigned PDC':{
        options: [],
        value:0
    },
    'Employee Email': {
        value:''
    },
    'Project Start Date': {
        value:''
    },
    'Ignore NH':{
        isChecked: false
    },
    'Path List':[],
    'PDC List': [],
    'Submit':{
        Color:'primary',
        Text:'Submit',
        ShowSpin: false
    },
    'Feedback':{
        color:'danger',
        code:'401',
        title:'UnAuthorized',
        text:'You are not authorized to do so',
        hidden: true
    }
};

const reducer = (state, action)=>{

    let newState = {...state};

    switch (action.type){

        case 'update-Value':{

            newState[action.label].value = action.value;

            newState['Feedback'].hidden = true;

            newState['Submit'].Text= 'Submit';

            return newState

            break
        }

        case 'mark-Checked':{

            newState[action.label].isChecked = action.value;

            return newState

            break;

        }

        case 'load-Options':{

            //console.dir(action.data);

            let assets = action.data.Assets;

            newState['Path List'] = assets;

            newState['Select Path'].options = assets.map(e=>{return{ID:e.ID,Name:e.Name}});
            
            newState['Select Path'].value = newState['Select Path'].options[0].ID;



            let pdcs = action.data.PDCs;

            newState['PDC List'] = pdcs;

            newState['Assigned PDC'].options = pdcs.map(e=>{return{ID:e.ID,Name:e.Name}});
            
            newState['Assigned PDC'].value = newState['Assigned PDC'].options[0].ID;


            return newState

            break;
        }

        case 'load-Response':{

        }

        case 'submit-Clicked':{

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;
            newState.Submit.ShowSpin = true;

            return newState

            break;
        }

        case 'submit-Error':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;
            newState.Submit.ShowSpin = false;


            return newState

            break;
        }

        case 'submit-Success':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;
            newState.Submit.ShowSpin = false;

            return newState

            break;

        }
        case 'load-Feedback':{

            newState['Feedback'].hidden = false;
            newState['Feedback'].color = action.color;
            newState['Feedback'].code = action.code;
            newState['Feedback'].title = action.title;
            newState['Feedback'].text = action.text;

            return newState;

            break;

        }

    }
};

export default function AssignPath() {

    const [state, dispatch] = useReducer(reducer,initState);



    useEffect(function(){

                const loadDropListOptions = async()=>{

                    let options = await fetch({method:'GET',endpoint:'Litmos/RetrieveNHAssets'});

                    if(options.didError){

                        //Do stuff

                        return

                    }

                    dispatch({type:'load-Options',data:options.data})

                }

                loadDropListOptions();
            
                return

            },[]
    );


    //This effect takes care of getting everything nice and ready to submit to the backend
    //It updates on any state change

        submitObject = {
            AssetID: state['Select Path'].value,
            Email:state['Employee Email'].value,
            IgnoreNH: state['Ignore NH'].isChecked,
            PDC: state['Assigned PDC'].value,
            StartDate: state['Project Start Date'].value
        }


  return (
    <main className={styles.main} style={{backgroundImage: `url(${backgroundImage.src})`}}>
      <Form>
        <FormTitle text='Assign Training Plan'></FormTitle>
        <FormDescription text={pageDescription}></FormDescription>
        <Select
            options={state['Select Path'].options}
            label='Select Path'
            value={state['Select Path'].value}
            dispatch={dispatch}
        ></Select>
        <Select
            options={state['Assigned PDC'].options}
            label='Assigned PDC'
            value={state['Assigned PDC'].value}
            dispatch={dispatch}
        ></Select>
        <EntryBox
            label='Employee Email'
            type = 'email'
            reminder ='Must Match Email In RMU'
            placeholder = ''
            value = {state['Employee Email'].value}
            dispatch={dispatch}
        ></EntryBox>
        <EntryBox
            label='Project Start Date'
            type = 'date'
            reminder =''
            placeholder = ''
            value = {state['Project Start Date'].value}
            dispatch={dispatch}
        ></EntryBox>
        <CheckBox
            label="Ignore NH"
            reminder='Check to Indicate Cross Training'
            isChecked={state['Ignore NH'].isChecked}
            dispatch={dispatch}
            hidden={false}
        ></CheckBox>
        <LongSubmit 
            method="POST"
            endpoint="Litmos/AssignLearningPath"
            dispatch={dispatch}
            data={submitObject}
            color={state.Submit.Color}
            text={state.Submit.Text}
            showspin={state.Submit.ShowSpin}
            ></LongSubmit>
            <Feedback
                color={state['Feedback'].color}
                code={state['Feedback'].code}
                title={state['Feedback'].title}
                text={state['Feedback'].text}
                hidden={state['Feedback'].hidden}
            ></Feedback>
      </Form>
    </main>
  );
}
